import styled from "styled-components";

import Select from "../../assets/UI/Select";
import { A } from "../../assets/styles/component";

import { EngLoc } from "../../assets/constants/localizations";

import logo_desk from "../../assets/images/Elements/logo_desktop.svg";

const StyledHeader = styled.header`
  display: flex;
  justify-content: center;
  align-items: center;

  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 6.938rem;

  border-bottom: 1px solid rgba(255, 255, 255, 0.1);

  font-weight: 400;
  font-size: 1.125rem;
  z-index: 999;

  transition: 0.25s;

  ${({ scrollY }) =>
    scrollY <= 100
      ? ""
      : `
    background: rgba(71, 8, 204, 0.65);
    height: 4.875rem;`}
`;

const Nav = styled.nav`
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  width: calc(100vw - 15%);
  max-width: 100rem;
  margin: 0 8.3%;

  @media (max-width: 1200px) {
    width: calc(100vw - 10%);
    margin: 0 3.3%;
  }
`;

const NavTabs = styled.div`
  display: flex;
  gap: clamp(1.875rem, 4vw, 4.125rem);

  & a {
    font-weight: 400;
    font-size: 1rem;
    line-height: 1.063rem;
  }
`;

const Logo = styled.img`
  aspect-ratio: 128 / 62;
`;

const Header = ({ navItem, language, scrollY, changeLng }) => {
  const changeLocalization = (lang = "Ru") => {
    localStorage.setItem("Language", lang);
    changeLng(lang);
  };

  return (
    <StyledHeader scrollY={scrollY}>
      <Nav>
        <Logo
          width="128"
          height="62"
          loading="lazy"
          src={logo_desk}
          alt="За стеклом"
        />
        <NavTabs>
          {navItem.map((item, index) => (
            <A
              href={item.href}
              size="18"
              key={index}
              target={item.blank && "_blank"}
              aria-label={"Перейти на раздел " + item.name}
            >
              {item.name}
            </A>
          ))}
        </NavTabs>
        <div style={{ display: "flex", gap: 15 + "px" }}>
          <Select
            type="languages"
            name={language === "Ru" ? "Ru" : "Eng"}
            paddingX="17"
            color="#FFFFFF"
            bg_color="rgba(255, 255, 255, 0.03)"
            border={true}
            onChange={changeLocalization}
          ></Select>
          <Select
            type="products"
            chevron={false}
            name={language === "Ru" ? "Войти" : EngLoc.buttons.login}
          ></Select>
        </div>
      </Nav>
    </StyledHeader>
  );
};

export default Header;
